.header {
    background-color: rgb(59, 54, 54);
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    position: sticky;

}

.header a {
    text-decoration: none;
    color: blanchedalmond;
    margin-right: 20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.mainheader {
    font-size: 2rem;
    color: azure;
    font-weight: bold;
    margin-left: 20px;
    /* font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif; */
}

.cards {
    width: 100%;
    margin: auto;
}

.card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px;
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
    background-color: #5c5757;
    width: 25.25%;
    border-radius: 12px;
    box-shadow: 0px 13px 10px -7px black;
    float: left;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.cardinfo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 120px;
    width: 338px;
    overflow: hidden;
}

.card:hover {
    box-shadow: 0px 30px 18px -8px black;
    transform: scale(1.05, 1.05);
}

.card img {
    height: 330px;
    width: 300px;
    border-radius: 20px;
    box-shadow: 0px 13px 10px -7px black;
    margin-top: 10px;
    margin-bottom: 10px;
}

.card img:hover {
    box-shadow: 0px 10px 48px 8px black;
}

.cardtitle {
    justify-content: center;
    align-items: center;
    /* margin-left: 40px; */
    margin-bottom: 10px;
    margin-top: 10px;
}

.download {
    background-color: rgb(46, 42, 42);
    color: aliceblue;
    border: none;
    height: 30px;
    width: 100px;
    border-radius: 20px;
    margin-bottom: 5px;
    /* margin-top: 10px; */
    /* margin-right: 20px; */
}

.download:hover {
    background-color: black;
    cursor: pointer;
}

#form input {
    background-color: #5c5757;
    height: 35px;
    width: 250px;
    border: none;
    border-radius: 20px;
    color: aliceblue;
}

.search {
    padding: 0.5rem 1rem;
    outline: 0;
    margin-left: 20px;
}



@media (max-width:450px){
    .menuitem{
        display: none;
    }
    .card{
        width: 84%;
        margin: 8%;
    }
    
}

@media (max-width:768px){
    .menuitem{
        display: none;
    }
    .card{
        margin-top: 10px;
        margin: 150px;
        width: 52%;
        margin-top: 43px;
        margin-left: 22%;
        margin-bottom: 40px;
    }
}